

const videoButton=document.querySelectorAll('.video__play-btn')
const videoTitle=document.querySelector('.title__video-content')
const playBtn=document.querySelectorAll(".play")
const videoNews=document.querySelector(".news__video")
const playBtn1=document.querySelectorAll(".play1")
const playBtn2=document.querySelectorAll(".gallery-play")
const InputOrder=document.querySelectorAll('.info__form input[name="input-order"]')
const blockOrder=document.querySelectorAll('.registration__block-togle')


InputOrder.forEach(item=>(
    item.addEventListener('change',function(){
        blockOrder.forEach(item=>{
            item.classList.remove('active')
        })
            
        
        if(item.checked){
            const currencyItem=item.getAttribute('data-radio')
            const currencyBlock=document.querySelector(currencyItem)
            currencyBlock.classList.add('active')
        }
    })
))

videoButton.forEach(item=>(
    item.addEventListener('click',function(){
   
    videoTitle.play()
    item.style.display='none'
    videoTitle.setAttribute('controls','controls')
    videoTitle.addEventListener('ended',function(){
        item.style.display='block'
       videoTitle.removeAttribute('controls','controls')
        videoTitle.load()
    })
})
))
playBtn.forEach(item=>{
    item.addEventListener('click',function(){
    
    videoNews.play()
    item.style.display='none'
    videoNews.setAttribute('controls','controls')
    videoNews.addEventListener('ended',function(){
        item.style.display='block'
       videoNews.removeAttribute('controls','controls')
       videoNews.load()
    })
})
})
playBtn1.forEach(item=>(
    item.addEventListener("click",function(){
        item.previousElementSibling.play()
        item.style.display='none'
        item.previousElementSibling.setAttribute('controls','controls')
        item.previousElementSibling.addEventListener('ended',function(){
            item.style.display='block'
            item.previousElementSibling.removeAttribute('controls','controls')
            item.previousElementSibling.load()
        })
    })
))
playBtn2.forEach(item=>{
    item.addEventListener("click",function(){
        item.previousElementSibling.play()
        item.style.display='none'
    item.previousElementSibling.setAttribute('controls','controls')
    item.previousElementSibling.addEventListener('ended',function(){
        item.style.display='block'
        item.previousElementSibling.removeAttribute('controls','controls')
        item.previousElementSibling.load()
        })
})
})


let tab = function(){
    let tabs = document.querySelectorAll('.payment__item')
    let blocks = document.querySelectorAll('.payment__block')
   tabs.forEach(item=>{

        item.addEventListener('click',tabsFunction)

   })
function tabsFunction(){
  tabs.forEach(tab=>{
    tab.classList.remove('active')
    
   }
  
  
   ) 

    this.classList.add('active')
    let currentItem=this.getAttribute('data-tab')
    SelectContent(currentItem)
}
 

function SelectContent(currentItem) {
    blocks.forEach(block=>{
        block.classList.contains(currentItem)?block.classList.add('active'):block.classList.remove('active')
        
       }
      
      
       ) 
    
}
}



tab()
const tabs=document.querySelectorAll(".registration__block-tab")
const registrBlock=document.querySelectorAll(".registration__right")

tabs.forEach(item=>(
   
    item.addEventListener("click",function(){
         tabs.forEach(item=>{
        item.classList.remove('active')
    })
        item.classList.add("active")
        const tab=item.getAttribute("data-tab")
        const tabBlock=document.querySelector(tab)
        registrBlock.forEach(item=>(item.classList.remove("active")))
        tabBlock.classList.add("active")
    })
))

